body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  text-align: center;
}
.game {
  font-size: 1.2em;
}
.game>div{
  margin-bottom: 20px;
}
.board{
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  width: 300px;
  height: 300px;
  position: relative;
  overflow: hidden;
} 
.cell{
  height: 99px;
  display: flex;
  flex: 1 0 25%;
  border: 1px solid;
  justify-content: space-around;
  align-items: center;
  font-size: 2.5em;
}
.board .cell:nth-child(1),.board .cell:nth-child(2),.board .cell:nth-child(3){
  border-top: 0;
} 
.board .cell:nth-child(3),.board .cell:nth-child(6),.board .cell:nth-child(9){
  border-right: 0;
} 
.board .cell:nth-child(7),.board .cell:nth-child(8),.board .cell:nth-child(9){
  border-bottom: 0;
} 
.board .cell:nth-child(1),.board .cell:nth-child(4),.board .cell:nth-child(7){
  border-left: 0;
} 
.newgame{
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.9);
}
.winner div {
  font-size: 2em;
  margin-bottom: 30px;
}
button{
  margin: 0 10px;
  width: 150px;
  height: 40px;
  border: none;
  outline: none;
  border-radius: 10px;
  font-size: 1.2em;
}
.redButton{
  background-color: #f16767;
}
.greenButton {
  background-color: #4fda66;
}